<template>
  <div>
    <!-- :visible.sync="dialogFormVisible" 控制显示跟隐藏 -->
    <el-dialog
      :title="isShow.edit ? '添加角色' : '修改角色'"
      :visible.sync="isShow.flag"
    >
      <!-- 通过 menu 组件传过来的 flag 进行 修改  -->

      <el-form ref="form" label-width="100px">
        <el-form-item label="角色名称">
          <el-input v-model="form.rolename"></el-input>
        </el-form-item>

        <el-form-item label="角色权限">
          <el-tree
            :data="menuGetter"
            show-checkbox
            node-key="id"
            :props="defaultProps"
            ref="tree"
          >
          </el-tree>
        </el-form-item>

        <el-form-item label="状态" prop="delivery">
          <!-- active-value="1" :inactive-value = "2" -->
          <!-- 设置 什么值打开 什么值关闭  -->
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >
        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { reqRoleAdd, reqRoleOne, reqRoleEdit } from "../../../util/request";
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";

export default {
  props: ["isShow"],
  data() {
    return {
      form: {
        rolename: "",
        status: 1,
        menus: "[]",
      },
      defaultProps: {
        children: "children",
        label: "title",
      },
    };
  },
  methods: {
    ...mapActions({
      actionrole: "role/actionrole",
      actionMenuList: "menu/actionMenuList",
    }),

    clear() {
      this.form = {
        rolename: "",
        status: 1,
        menus: "[]",
      };
      // 清空 树形控件
      this.$refs.tree.setCheckedKeys([]);
    },

    // 点击取消之后进行清空
    cancel() {
      this.isShow.flag = false;
      this.clear();
    },

    // 点击添加执行结果
    add() {
      // 后台要的是 字符串
      // this.$refs.tree.getCheckedKeys()请看文档
      this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
      reqRoleAdd(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();

          // 重新调用刷新
          this.actionrole();
        } else {
          warningAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();
        }
      });
    },
    // 获取一个
    getOne(id) {
      // // 首先应该先获取其中一条 然后进行 先渲染 然后在进行提交修改
      // // 1.获取其中 一条
      reqRoleOne(id).then((res) => {
        this.$refs.tree.setCheckedKeys(JSON.parse(res.data.list.menus));
        this.form = res.data.list;
        this.form.id = id;
      });
    },

    // 修改点击提交
    edit() {
      this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
      // console.log(this.form);
      reqRoleEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();

          // 重新调用刷新
          this.actionrole();
        } else {
          warningAlert(res.data.msg);
          this.isShow.flag = false;
          this.clear();
        }
      });
    },
  },

  computed: {
    ...mapGetters({
      gettersrole: "role/gettersrole",
      menuGetter: "menu/menuGetter",
    }),
  },
  mounted() {
    this.actionMenuList();
  },
};
</script>

<style>
</style>


