<template>
  <div class="listmenu">
    <el-table
      :data="gettersrole"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="角色编号" sortable width="150">
      </el-table-column>
      <el-table-column
        prop="rolename"
        label="角色名称"
        sortable
        width="300"
      ></el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <div>
            <!-- 这里自定义 表格 在template中 添加 slot-scope = scope -->
            <!-- 判断状态 如果说 后台返回的 status 是 1 就是启用 否则就禁用  -->
            <el-button type="primary" v-if="scope.row.status == 1" plain
              >启用</el-button
            >
            <el-button type="info" v-else plain >禁用</el-button>

            <el-button type="success" plain @click="edit(scope.row.id) ">编辑</el-button>

            <delbutton @delbtn="del(scope.row.id)"></delbutton>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
// 引入仓库
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import { reqRoleDel} from "../../../util/request";
export default {

  computed: {
    ...mapGetters({
      gettersrole: "role/gettersrole",
    }),
  },
  methods: {
    ...mapActions({
      actionrole: "role/actionrole",
    }),
    // 删除
    del(id) {
      
      reqRoleDel(id).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionrole();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
    // 修改
    edit(id) {
      this.$emit("delid",id)
    }
  },
  mounted() {
    this.actionrole();
  },
};
</script>

<style lang="">
.cell {
  text-align: center;
}

.el-table,
.submit {
  width: 100%;
  margin-top: 100px;
}
.submit {
  font-size: 16px;
}
</style>


